<template><section><html><head></head><body><h1>GridImageryProvider</h1>
<p><code v-pre="">vc-provider-imagery-grid</code> 用于添加具有可控制背景和辉光的线框网格，对于自定义渲染效果或调试地形可能很有用。</p>
<h2>示例</h2>
<h3>加载经纬格网图层</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-layer-imagery :alpha="alpha" :brightness="brightness" :contrast="contrast">
          <vc-provider-imagery-grid></vc-provider-imagery-grid>
        </vc-layer-imagery>
      </vc-viewer>
      <div class="demo-tool">
        <span>透明度</span>
        <vue-slider v-model="alpha" :min="0" :max="1" :interval="0.01"></vue-slider>
        <span>亮度</span>
        <vue-slider v-model="brightness" :min="0" :max="3" :interval="0.01"></vue-slider>
        <span>对比度</span>
        <vue-slider v-model="contrast" :min="0" :max="3" :interval="0.01"></vue-slider>
      </div>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
