      <span class="hljs-tag">&lt;<span class="hljs-name">vc-layer-imagery</span> <span class="hljs-attr">:alpha</span>=<span class="hljs-string">"alpha"</span> <span class="hljs-attr">:brightness</span>=<span class="hljs-string">"brightness"</span> <span class="hljs-attr">:contrast</span>=<span class="hljs-string">"contrast"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-provider-imagery-grid</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-provider-imagery-grid</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-layer-imagery</span>&gt;</span>
