<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>tilingScheme</td>
<td>Object</td>
<td></td>
<td><code v-pre="">required</code> 指定瓦片坐标系信息。</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定参考椭球体。默认参考椭球WGS84。</td>
</tr>
<tr>
<td>cells</td>
<td>Number</td>
<td><code v-pre="">8</code></td>
<td><code v-pre="">optional</code> 指定网格单元格数量。</td>
</tr>
<tr>
<td>color</td>
<td>Object|String|Array</td>
<td><code v-pre="">[1.0, 1.0, 1.0, 0.4]</code></td>
<td><code v-pre="">optional</code> 指定网格线的颜色。</td>
</tr>
<tr>
<td>glowColor</td>
<td>Object|String|Array</td>
<td><code v-pre="">[0.0, 1.0, 0.0, 0.05]</code></td>
<td><code v-pre="">optional</code> 指定网格线发光的颜色。</td>
</tr>
<tr>
<td>glowWidth</td>
<td>Number</td>
<td><code v-pre="">6</code></td>
<td><code v-pre="">optional</code> 指定渲染发光效果的宽度。</td>
</tr>
<tr>
<td>backgroundColor</td>
<td>Object|String|Array</td>
<td><code v-pre="">[0.0, 0.5, 0.0, 0.2]</code></td>
<td><code v-pre="">optional</code> 指定填充背景色。</td>
</tr>
<tr>
<td>tileWidth</td>
<td>Number</td>
<td><code v-pre="">256</code></td>
<td><code v-pre="">optional</code> 指定瓦片到最详细级别的宽度。</td>
</tr>
<tr>
<td>tileHeight</td>
<td>Number</td>
<td><code v-pre="">256</code></td>
<td><code v-pre="">optional</code> 指定瓦片到最详细级别的高度。</td>
</tr>
<tr>
<td>canvasSize</td>
<td>Number</td>
<td><code v-pre="">256</code></td>
<td><code v-pre="">optional</code> 指定渲染画布大小。</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>参考官方文档： <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/GridImageryProvider.html">GridImageryProvider</a></strong></li>
</ul>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>errorEvent</td>
<td>TileProviderError</td>
<td>当图层提供者发生异步错误时触发, 返回一个 TileProviderError 实例。</td>
</tr>
<tr>
<td>readyPromise</td>
<td>ImageryProvider</td>
<td>当图层提供者准备好时触发。</td>
</tr>
</tbody>
</table>
